import { ReactElement } from "react";
import { IconSectionModel } from "./models";
import { Link } from "react-router-dom";
import userIcon from "../../img/user.png";
import laptopIcon from "../../img/laptop.png";
import proyectIcon from "../../img/project-management.png";
import socialIcon from "../../img/social.png";
import "./style.css";

const IconSection = (props: IconSectionModel): ReactElement => {
	const getText = (text: string): ReactElement => {
		const splitText: string[] = text.split(" ");
		const isBreak = splitText.length > 1;
		if (isBreak) {
			return (
				<>
					<span className="break">{splitText[0]}</span> {splitText[1]}
				</>
			);
		} else {
			return <>{text}</>;
		}
	};

	const textContent: ReactElement = getText(props.text);
	return (
		<Link to={props.src} className="section-item">
			<li className="text-center">
				<img className="data-icon" src={props.icon} alt={props.iconAlt} /> <br />
				<p className="text-white">{textContent}</p>
			</li>
		</Link>
	);
};

const NavbarIcon = ({ navMenu }: { navMenu: string }): ReactElement => {
	const iconSections: IconSectionModel[] = [
		{
			icon: userIcon,
			text: "Sobre mi",
			iconAlt: "Icono de la sección sobre mi",
			src: "/",
		},
		{
			icon: laptopIcon,
			text: "Skills",
			iconAlt: "Icono de la sección skills",
			src: "/skills",
		},
		{
			icon: proyectIcon,
			text: "Proyectos",
			iconAlt: "Icono de la sección skills",
			src: "/projects",
		},
		{
			icon: socialIcon,
			text: "Redes Sociales",
			iconAlt: "Icono de la sección redes sociales",
			src: "/social",
		},
	];

	const sections: ReactElement[] = iconSections.map((section: IconSectionModel) => {
		return (
			<IconSection
				key={section.text}
				icon={section.icon}
				iconAlt={section.iconAlt}
				text={section.text}
				src={section.src}
			></IconSection>
		);
	});
	return (
		<div id={navMenu} className="collapse navbar-collapse">
			<ul className="d-flex justify-content-between ml-md-auto u-list">{sections}</ul>
		</div>
	);
};

export default NavbarIcon;
