import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import { NavbarMenu, Contact, AboutMe, Skills, Projects, Social } from "./components";
import chatLogo from "./img/chat.png";
import "./App.css";
import "./index.css";

function App() {
	return (
		<div className="App">
			<Router>
				<header>
					<NavbarMenu />
				</header>
				<div className="content">
					<Switch>
						<Route path="/" component={AboutMe} exact />
						<Route path="/skills" component={Skills} />
						<Route path="/projects" component={Projects} />
						<Route path="/social" component={Social} />
						<Route path="/*" component={AboutMe} />
					</Switch>
				</div>
			</Router>

			<a
				id="button-icon"
				href="#contact-section"
				className="text-dark"
				data-toggle="collapse"
				aria-expanded="false"
			>
				<img src={chatLogo} alt="boton para abrir la sección de contacto" />
			</a>
			<Contact idContactSection="contact-section"></Contact>
		</div>
	);
}

export default App;
